import { Component, OnInit, ViewContainerRef } from "@angular/core";
import { Router } from '@angular/router';
import { AccountService } from '../services/account.service';
import { User, UserLogin, Token } from '../shared/model';
import { StorageService } from "../services/storage.service";
import { Constants } from "../shared/Constants";
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { LoaderService } from "../services/loader.service";
import { LocalStorageService } from "angular-2-local-storage";


@Component({
  selector: "login",
  templateUrl: "./login.component.html",
  styleUrls: []
})

export class Login implements OnInit { //


  errorMessage: string = "";

  users: User[] = new Array<User>();
  headers: string[] = new Array<string>();

  public login: UserLogin = new UserLogin();
  public token: Token = new Token();
  private userId: number;

  constructor(private router: Router, private account: AccountService, private storage: StorageService,
    public toastr: ToastsManager, private vcr: ViewContainerRef, private loaderService: LoaderService,
    private localStorageService: LocalStorageService) {
    this.toastr.setRootViewContainerRef(vcr);
  }

  ngOnInit(): void {
    try {
      var token = this.storage.Token;

      if (token.loginRequired) {

        this.router.navigate(['']);
      }
      else {
        this.storage.Clear();
        this.router.navigate(['/login']);
      }
    }
    catch
    {
      this.storage.Clear();
      this.router.navigate(['/login']);
    }
  }

  public onLogin(login: UserLogin) {

    //http call starts
    this.loaderService.display(true);
    var self = this;

    this.account.getUserToken(login).subscribe(resp => {
  
      if (resp.Succeeded) {

        self.token.token = resp.Result.token;
        self.token.expiration = resp.Result.expiration;
        self.token.user = resp.Result.user;

        // log into local storage
        self.storage.Token = self.token;
        self.storage.User = resp.Result.user;

        this.toastr.success('Successfully sign in', 'Success!');
        self.loaderService.display(false);
        this.router.navigate(['']);
      }
      else {
        this.loaderService.display(false);
        this.router.navigate(['']);
      }
    },
      error => {

        this.storage.Clear();
        this.toastr.error('Failed to login', 'Success!');
        this.loaderService.display(false);
        this.router.navigate(['']); 

      });
  }

  KeyPress($event) {
    //If Enter was Pressed
    if ($event.keyCode == 13) {
      //this.SignUp();
    }
  }

  public Redirect(role: string) {

    if (role == "Administration") {
      this.toastr.info(role);
      this.router.navigate(['/']);
    }
    else if (role == Constants.Roles.User) {
      this.router.navigate(['/login']);
    }
  }
 

}



