import { Injectable, OnInit, ViewContainerRef } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from "rxjs";
import "rxjs/add/operator/map";
import { UserLogin, User, Token, Operation } from '../shared/model';
import { HttpClient, HttpResponse, HttpErrorResponse, HttpHeaders, HttpEvent } from '@angular/common/http';
import { StorageService } from './storage.service';
import { Constants } from '../shared/Constants';
import { Router } from '@angular/router';
import { catchError, retry, tap } from 'rxjs/operators';
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import { ToastsManager } from 'ng2-toastr';
import { LoaderService } from './loader.service';
import { LocalStorageService } from 'angular-2-local-storage';

@Injectable()
export class DataService implements OnInit {

  ngOnInit(): void {

  }

  private users: User[] = [];

  public token: Token = new Token();
  public userId: number;

  constructor(private http: HttpClient, private storage: StorageService, private router: Router,
    public loaderService: LoaderService, private localStorageService: LocalStorageService) { //private toastr: ToastsManager, private vcr: ViewContainerRef,

    //this.toastr.setRootViewContainerRef(vcr);
  }

  //public httpOptions = {
  //  headers: new HttpHeaders({
  //    //'Content-Type': 'application/json',
  //    'Authorization': `Bearer ${this.localStorageService.get<Token>(Constants.OAuthTokenKey)}`
  //  })
  //};


  public Get<T>(url: string, data?: any): Observable<Operation<T>> {
    //const options = term ?
    //  { params: new HttpParams().set('name', term) } : {};
    var _token = this.storage.Token;

    var httpOption = { headers: new HttpHeaders({ "Authorization": "Bearer " + _token.token }) };

    this.loaderService.display(true);
    //console.log(httpOption);
   // console.log(url);
    return this.http.get<Operation<T>>(url, httpOption).pipe(
      tap( // Log the result or error
        data => {
          this.log(url, data);
          this.loaderService.display(false);
        },
        error => {
          this.logError(url, error);
          this.loaderService.display(false);
        }
      ),
      catchError(this.handleError));
  }
  public Post<T>(url: string, data?: any): Observable<Operation<T>> {
   
    var _token = this.storage.Token;
    var httpOption = { headers: new HttpHeaders({ "Authorization": "Bearer " + _token.token }) };
    this.loaderService.display(true);
    return this.http.post<Operation<T>>(url, data, httpOption).pipe(
      tap( // Log the result or error
        data => {
          this.log(url, data);
          this.loaderService.display(false);
        },
        error => {
          this.logError(url, error);
          this.loaderService.display(false);
        }
      ),
      catchError(this.handleError));;
  }
  public Put<T>(url: string, data?: any): Observable<Operation<T>> {

    var token =  this.storage.Token;

    var httpOption = { headers: new HttpHeaders({ "Authorization": "Bearer " + token.token }) };
    this.loaderService.display(true);
    return this.http.put<Operation<T>>(url, data, httpOption).pipe(
      tap( // Log the result or error
        data => {
          this.log(url, data);
          this.loaderService.display(false);
        },
        error => {
          this.logError(url, error);
          this.loaderService.display(false);
        }
      ),
      catchError(this.handleError));;
  }
  public Delete<T>(url: string, data?: any): Observable<Operation<T>> {

    var token  = this.storage.Token;

    var httpOption = { headers: new HttpHeaders({ "Authorization": "Bearer " + token.token }) };
    this.loaderService.display(true);
    return this.http.delete<Operation<T>>(url, httpOption).pipe(
      tap( // Log the result or error
        data => {
          this.log(url, data);
          this.loaderService.display(false);
        },
        error => {
          this.logError(url, error);
          this.loaderService.display(false);
        }
      ),
      catchError(this.handleError));;
  }

  public GetWithOutToken<T>(url: string, data?: any): Observable<Operation<T>> {
    this.loaderService.display(true);
    return this.http.get<Operation<T>>(url).pipe(
      tap( // Log the result or error
        data => {
          this.log(url, data);
          this.loaderService.display(false);
        },
        error => {
          this.logError(url, error);
          this.loaderService.display(false);
        }
      ),
      catchError(this.handleError));;
  }
  public PostWithOutToken<T>(url: string, data?: any): Observable<Operation<T>> {
    this.loaderService.display(true);
    return this.http.post<Operation<T>>(url, data).pipe(
      tap( // Log the result or error
        data => {
          this.log(url, data);
          this.loaderService.display(false);
        },
        error => {
          this.logError(url, error);
          this.loaderService.display(false);
        }
      ),
      catchError(this.handleError));
  }

  public PostBlob<T>(url: string, blob: any): Observable<HttpEvent<T>> {
    this.loaderService.display(true);
    return this.http.post<T>(url, { responseType: 'blob' }, blob).pipe(
      tap( // Log the result or error
        data => {
          this.log(url, data);
          this.loaderService.display(false);
        },
        error => {
          this.logError(url, error);
          this.loaderService.display(false);
        }
      ),
      catchError(this.handleError));
  }

  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      //this.toastr.error('An error occurred:', error.error.message, 'Error');
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,

      //this.toastr.error(`Backend returned code ${error.status}, ` +
      //  `body was: ${error.error}`, 'Error');


    }

    // return an ErrorObservable with a user-facing error message
    //this.toastr.error('Something bad happened; please try again later.', 'Error');

    return new ErrorObservable(
      'Something bad happened; please try again later.');
  };

  private _handleError(err: HttpErrorResponse | any) {
    const errorMsg = err.message || 'Unable to retrieve data';
    return Observable.throw(errorMsg);
  }

  private log(url: string, data?: any) {
    //console.log(`Http operation was successfully called ${url}`);
  }

  private logError(url: string, data?: any) {

    console.log(`Http operation failed ${url}`);
  }
}



