import { Component, Input, OnChanges, OnInit, SimpleChanges, ViewContainerRef } from "@angular/core";
import { Router } from "@angular/router";
import { Category, AlertType } from "../shared/model";
import { ToastsManager } from "ng2-toastr";
import { LoaderService } from "../services/loader.service";
import { DataService } from "../services/data.service";
import { CategoryService } from "../services/category.service";
import { SequencePage } from "../shared/sequencepage";
import { HttpRequest, HttpEventType, HttpResponse, HttpClient } from "@angular/common/http";
import { forEach } from "@angular/router/src/utils/collection";
import { $ } from "protractor";

@Component({
  selector: "category",
  templateUrl: "./category.component.html"
  //changeDetection: ChangeDetectionStrategy.OnPush
})

export class CategoryComponent implements OnInit, OnChanges {


  public category: Category = new Category();
  @Input()
  public data: SequencePage<Category>;

  private pageIndex: number = 1;
  private pageSize: number = 100;

  public pageLinks: number[] = [];
  public hasPage: boolean;

  @Input()
  public categories: Category[] = new Array<Category>();
  public formData: FormData = new FormData();
  public fileName: string;
  public uploadProgress: number;
  public name: string;

  constructor(private categoryService: CategoryService, private router: Router, public toastr: ToastsManager, private vcr: ViewContainerRef, private loaderService: LoaderService, private http: HttpClient) {
    this.toastr.setRootViewContainerRef(vcr);
  }

  ngOnInit(): void {
    this.LoadFirstPage();
    // throw new Error("Method not implemented.");
  }

  ngOnChanges(changes: SimpleChanges): void {
    // only run when property "data" changed
    if (changes['categories']) {
      this.categories = this.Get(0);
    }
  }
  onFileChanged(files) {

    this.formData = new FormData();

    if (!files) {
      this.toastr.error("Error, please check the file inputs ");
      return;
    }

    for (let file of files) {
      this.formData.append(file.name, file);
      this.fileName = file.name;
    }

  }

  LoadFirstPage() {
    var get = this.Get(0);
    this.hasPage = this.HasNoPage();
    return get;
  }

  LoadLastPage() {

    if (this.data) {
      return this.Get((Math.ceil(this.data.SequenceLength / this.pageSize) - 1));
    }
  }

  HasNoPage(): boolean {
    var result: boolean = this.data == null || typeof this.data == 'undefined' || this.data.Page.length == 0;
    return result;
  }

  public Get(pageIndex?: number): Array<Category> {
    var self = this;
    this.loaderService.display(true);

    this.categoryService.GetCategories(pageIndex, this.pageSize).subscribe(resp => {
      var _page = resp.Result;

      if (_page.PageSize == 0) {
        //<-- debug stuff
        self.pageLinks = [];
      }
      else {
        self.data = new SequencePage<Category>(_page.Page, _page.SequenceLength, _page.PageSize, _page.PageIndex);
        self.pageLinks = self.data.AdjacentIndexes(2);
        self.categories = self.data.Page;
        return self.categories;
      }

      this.loaderService.display(false);
    }, err => {
      self.data = new SequencePage<Category>([], 0);
      self.pageLinks = self.data.AdjacentIndexes(2);
      this.toastr.error('Operation failed e.g UnAuthorized or Network Issue');
      this.loaderService.display(false);
    });

    return self.categories;
  }

  public Ok() {

    var self = this;

    if (!this.category.Name) {
      this.toastr.error('Operation failed,category name cannot be empty');
      return;
    }

    if (!this.category) {
      this.toastr.error('Operation failed,please check the category name');
      return;
    }

    this.loaderService.display(true);

    var self = this;
    this.categoryService.AddOrUpdateCategory(this.category).subscribe(c => {

      if (c.Succeeded) {

        self.category.Name = null;

        const req = new HttpRequest('POST', `api/category/file/${c.Result.CategoryId}`, self.formData, {
          reportProgress: true,
        });

        //self.toastr.success('Operation is successful');

        self.http.request(req).subscribe(event => {


          self.formData = new FormData();
          // self.toastr.success('Operation is successful');

          if (event.type === HttpEventType.UploadProgress) {
            self.uploadProgress = Math.round(100 * event.loaded / event.total);

            // self.product.ProductId = 0;
            self.loaderService.display(false);
            self.Get(0);
          }
          else if (event instanceof HttpResponse) {
            console.log('Files uploaded!');
            self.Get(0);
            // self.toastr.success('Operation is successful');
          }
          self.loaderService.display(false);
        });
      }
      else {
        self.toastr.error('Operation failed');
      }

      // $('#catfileupload')
      this.loaderService.display(false);

    });
  }


  public onUpdate(id: number) {
    this.category = this.categories.filter(c => c.CategoryId == id).pop();//.filter(n => this.name = n.Name);
    this.router.navigate(['/category']);
  }

  public onDelete(id: number) {
    var op = confirm('Are you sure?');

    if (op) {

      this.categoryService.DeleteCategory(id).subscribe(c => {
        this.categories = this.Get(0);
        this.toastr.success("Operation Successful");
        this.router.navigate(['/category']);
      });
    }
  }
}



