import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from "rxjs";
import "rxjs/add/operator/map";
import { Category, Operation, Product, UserQuestion, Question } from '../shared/model';
import { HttpClient, HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { StorageService } from './storage.service';
import { Constants } from '../shared/Constants';
import { Router } from '@angular/router';
import { catchError, retry } from 'rxjs/operators';
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import { DataService } from './data.service';
import { SequencePage } from '../shared/sequencepage';

@Injectable()
export class QuestionService {

  private question: UserQuestion[] = new Array<UserQuestion>();

  constructor(private data: DataService, private storage: StorageService) {

  }

  public GetQuestionPage(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<UserQuestion>>> {

    var url = `/api/question/${pageIndex}/${pageSize}`;
    console.log(url);
    return this.data.Get<SequencePage<UserQuestion>>(url);
  }


  public getQuestions(): Observable<Operation<UserQuestion[]>> {
    return this.data.Get<UserQuestion[]>("/api/question");
  }

  public getQuestionById(id: number): Observable<Operation<UserQuestion>> {
    return this.data.Get<UserQuestion>("/api/question/" + id);
  }

  public AddOrUpdateQuestion(question: UserQuestion): Observable<Operation<UserQuestion>> {
    let url = `/api/question`;
    var result = this.data.Post<UserQuestion>(url, question);
    return result;
  }

  public DeleteQuestion(id: number): Observable<Operation<UserQuestion>> {

    let url = '/api/question/' + id;

    return this.data.Delete<UserQuestion>(url);
  }


}



