import { Component, OnInit, ViewContainerRef, Input, OnChanges, SimpleChanges, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { Category, Product, Advert } from "../shared/model";
import { ToastsManager } from "ng2-toastr";
import { LoaderService } from "../services/loader.service";
import { DataService } from "../services/data.service";
import { CategoryService } from "../services/category.service";
import { SequencePage } from "../shared/sequencepage";
import { HttpRequest, HttpEventType, HttpResponse, HttpClient } from "@angular/common/http";
import { AdvertService } from "../services/advert.service";

@Component({
  selector: "advert",
  templateUrl: "./advert.component.html",
})

export class AdvertComponent implements OnInit, OnChanges {


  public serviceBase: string = location.protocol + "//" + location.host + "/";
  public product: Product = new Product();

  @Input()
  public data: SequencePage<Advert>;

  public pageIndex: number = 1;
  public pageSize: number = 100;

  public pageLinks: number[] = [];
  public hasPage: boolean;

  public categories: Category[] = new Array<Category>();

  public adverts: Advert[] = new Array<Advert>();
  public advert: Advert = new Advert();

  public formData: FormData = new FormData();
  public uploadProgress: number;
  public fileName: string;

  public advertId: number;



  constructor(private services: AdvertService, private categoryService: CategoryService, private router: Router,
    public toastr: ToastsManager, private vcr: ViewContainerRef, private loaderService: LoaderService, private http: HttpClient) {
    this.toastr.setRootViewContainerRef(vcr);
  }


  ngOnInit(): void {
    this.LoadFirstPage();
    this.categoryService.getCategory().subscribe(c => {

      if (c.Succeeded) {
        this.categories = c.Result;
      }
    })
    // throw new Error("Method not implemented.");
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['adverts']) {
      this.adverts = this.Get(0);
    }
  }

  LoadFirstPage() {
    var get = this.Get(0);
    this.hasPage = this.HasNoPage();
    return get;
  }

  LoadLastPage() {
    if (this.data) {
      return this.Get((Math.ceil(this.data.SequenceLength / this.pageSize) - 1));
    }
  }

  HasNoPage(): boolean {
    var result: boolean = this.data == null || typeof this.data == 'undefined' || this.data.Page.length == 0;
    return result;
  }

  public Get(pageIndex?: number): Array<Advert> {

    var self = this;
    this.loaderService.display(true);

    this.services.GetAdverts(pageIndex, this.pageSize).subscribe(resp => {
      var _page = resp.Result;

      if (_page.PageSize == 0) {
        //<-- debug stuff
        self.pageLinks = [];
      }
      else {

        self.data = new SequencePage<Advert>(_page.Page, _page.SequenceLength, _page.PageSize, _page.PageIndex);
        self.pageLinks = self.data.AdjacentIndexes(2);
        console.log(self.pageLinks);
        self.adverts = self.data.Page;
      }

      this.loaderService.display(false);
      return self.adverts;
    }, err => {
      self.data = new SequencePage<Advert>([], 0);
      self.pageLinks = self.data.AdjacentIndexes(2);
      //this.toastr.error('Operation failed e.g UnAuthorized or Network Issue');
      this.loaderService.display(false);
      return self.adverts;
      });
    return self.adverts;
  }

  public onUpdate(id: number) {
    this.advert = this.adverts.filter(c => c.AdvertId == id).pop();//.filter(n => this.name = n.Name);
    this.router.navigate(['/advert']);
  }

  public onDelete(id: number) {
    this.services.DeleteAdvert(id).subscribe(c => {
      this.adverts = this.Get(0);
      this.toastr.success("Operation Successful");
      this.router.navigate(['/advert']);
    });
  }


  onFileChanged(files) {
    if (!files) {
      this.toastr.error("Error, please check Image to upload ");
      return;

    }

    // const formData = new FormData();

    for (let file of files) {
      this.formData.append(file.name, file);
      this.fileName = file.name;
    }

    //this.files = event.target.files;
  }

  onFileUpload() {
    const formData = new FormData();
    //for (const file of this.files) {
    //  formData.append(name, file, file.name);
    //}
    //this.http.post('url', formData).subscribe(x => ....);
  }

  public Ok() {

    var self = this;

    if (!this.advert.Description) {
      this.toastr.error("Error, please check  Description ");
      return;
    }

    if (!this.advert.Name) {
      this.toastr.error("Error, please check advert  Name ");
      return;
    }
  

    this.loaderService.display(true);

    var self = this;

    self.services.AddOrUpdateAdvert(this.advert).subscribe(c => {

      if (c.Succeeded) {

        if (c.Succeeded) {

          this.advertId = c.Result.AdvertId;

          const req = new HttpRequest('POST', `api/advert/file/${self.advertId}`, self.formData, {
            reportProgress: true,
          });

          self.toastr.success('Operation is successful');

          self.http.request(req).subscribe(event => {
            self.adverts = self.Get(0);

            self.formData = new FormData();

            if (event.type === HttpEventType.UploadProgress) {
              self.uploadProgress = Math.round(100 * event.loaded / event.total);

              // self.product.ProductId = 0;
              self.loaderService.display(false);
            }
            else if (event instanceof HttpResponse) {
              console.log('Files uploaded!');
              // self.toastr.success('Operation is successful');
            }

         // $('#catfileupload')
            self.loaderService.display(false);
          });
        }
        else {
          self.toastr.error('Operation failed');
          self.fileName = null;
          self.formData = null;
        }
        self.loaderService.display(false);

        self.fileName = null;
        self.formData = null;
        self.formData = new FormData();
        self.advert.Description = '';
        self.advert.ImageUrl = '';
        self.advert.Name = '';
      }
      else {
        self.toastr.error('Operation failed');
      }
      self.loaderService.display(false);


    }, error => {
      self.toastr.error('Operation failed,please check the inputs');
    });





  }
}



