import { Component, OnInit, ViewContainerRef, Input, OnChanges, SimpleChanges, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { Category, Product } from "../shared/model";
import { ToastsManager } from "ng2-toastr";
import { LoaderService } from "../services/loader.service";
import { DataService } from "../services/data.service";
import { CategoryService } from "../services/category.service";
import { SequencePage } from "../shared/sequencepage";
import { ProductService } from "../services/product.services";
import { HttpRequest, HttpEventType, HttpResponse, HttpClient } from "@angular/common/http";

@Component({
  selector: "manage-product",
  templateUrl: "./product.component.html",
})

export class ProductComponent implements OnInit, OnChanges {



  public product: Product = new Product();

  @Input()
  public data: SequencePage<Product>;

  public pageIndex: number = 1;
  public pageSize: number = 100;

  public pageLinks: number[] = [];
  public hasPage: boolean;

  public categories: Category[] = new Array<Category>();

  public products: Product[] = new Array<Product>();

  public formData: FormData = new FormData();
  public uploadProgress: number;
  public fileName: string;

  public categoryId: number;
  public productName: string;
  public Price: string;
  public productId: number = 0;

  constructor(private services: ProductService, private categoryService: CategoryService, private router: Router,
    public toastr: ToastsManager, private vcr: ViewContainerRef, private loaderService: LoaderService, private http: HttpClient) {
    this.toastr.setRootViewContainerRef(vcr);
  }

  ngOnInit(): void {
    this.LoadFirstPage();
    this.categoryService.getCategory().subscribe(c => {

      if (c.Succeeded) {
        this.categories = c.Result;
      }
    })
    // throw new Error("Method not implemented.");
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['products']) {
      this.products = this.Get(0);
    }
  }

  LoadFirstPage() {
    var get = this.Get(0);
    this.hasPage = this.HasNoPage();
    return get;
  }

  LoadLastPage() {

    if (this.data) {
      return this.Get((Math.ceil(this.data.SequenceLength / this.pageSize) - 1));
    }
  }

  HasNoPage(): boolean {
    var result: boolean = this.data == null || typeof this.data == 'undefined' || this.data.Page.length == 0;
    return result;
  }

  public Get(pageIndex?: number): Array<Product> {

    var self = this;
    this.loaderService.display(true);

    this.services.GetProducts(pageIndex, this.pageSize).subscribe(resp => {
      var _page = resp.Result;

      if (_page.PageSize == 0) {
        //<-- debug stuff
        self.pageLinks = [];
      }
      else {

        self.data = new SequencePage<Product>(_page.Page, _page.SequenceLength, _page.PageSize, _page.PageIndex);
        self.pageLinks = self.data.AdjacentIndexes(2);
        console.log(self.pageLinks);
        self.products = self.data.Page;
      }

      this.loaderService.display(false);
      return self.products;
    }, err => {
      self.data = new SequencePage<Product>([], 0);
      self.pageLinks = self.data.AdjacentIndexes(2);
      //this.toastr.error('Operation failed e.g UnAuthorized or Network Issue');
      this.loaderService.display(false);
      return self.products;
    });
    return self.products;
  }


  public onUpdate(id: number) {

    var self = this;

    this.services.getProductById(id).subscribe(c => {
      self.product = c.Result;
      self.productName = c.Result.ProductName;
      self.Price = c.Result.Price;
      self.categoryId = c.Result.CategoryId;
      self.productId = c.Result.ProductId;
      self.categoryId = c.Result.CategoryId;

      console.log(c.Result);
    })
    //this.product = this.products.filter(c => c.ProductId == id).pop();//.filter(n => this.name = n.Name);
    console.log(self.product);
    this.router.navigate(['/product']);
  }

  public onDelete(id: number) {

    var op = confirm('Are you sure?');

    if (op) {
      this.services.DeleteProduct(id).subscribe(c => {
        this.products = this.Get(0);
        this.toastr.success("Operation Successful");
        this.router.navigate(['/product']);
      });
    }
  }

  onFileChanged(files) {

    if (!files) {
      this.toastr.error("Error, please check the file inputs ");
      return;
    }

    // const formData = new FormData();

    for (let file of files) {
      this.formData.append(file.name, file);
      this.fileName = file.name;
    }

    //this.files = event.target.files;
  }

 

  public Ok() {

    var self = this;

    if (!this.productName || !this.Price || !this.categoryId) {

      console.log(this.productName);
      console.log(this.Price);
      console.log(this.categoryId);

      this.toastr.error("Error, please check your inputs ");
      return;
    }

    this.loaderService.display(true);

    var self = this;

    this.product.CategoryId = this.categoryId;
    this.product.ProductName = this.productName;
    this.product.Price = this.Price;
    this.product.ProductId = this.productId;


    self.services.AddOrUpdateProduct(self.product).subscribe(c => {

      if (c.Succeeded) {

        self.product.ProductName = null;
        self.product.Price = null;
        self.product.CategoryId = null;

        const req = new HttpRequest('POST', `api/product/file/${c.Result.ProductId}`, self.formData, {
          reportProgress: true,
        });

        self.toastr.success('Operation is successful');

        self.http.request(req).subscribe(event => {

          self.Get(0);
          //sself.formData = null;
           self.formData = new FormData();
          self.productId = 0;
          self.categoryId = 0;

          if (event.type === HttpEventType.UploadProgress) {
            self.uploadProgress = Math.round(100 * event.loaded / event.total);

            // self.product.ProductId = 0;
            self.loaderService.display(false);
          }
          else if (event instanceof HttpResponse) {
            console.log('Files uploaded!');
            // self.toastr.success('Operation is successful');
          }
          self.loaderService.display(false);
        });
      }
      else {
        self.toastr.error('Operation failed');
      }
      self.loaderService.display(false);

      self.productName = null;
      self.Price = null;
      self.fileName = null;
      self.productId = 0;
      self.formData = null;
      self.product.ProductId = null;

    }, error => {
      self.toastr.error('Operation failed,please check the inputs');
    });





  }
}



