
import { Component, OnInit, ViewContainerRef, Input, OnChanges, SimpleChanges, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { Category, Product, UserQuestion, WalletPoint, Receipt, WalletModel } from "../shared/model";
import { ToastsManager } from "ng2-toastr";
import { LoaderService } from "../services/loader.service";
import { DataService } from "../services/data.service";
import { CategoryService } from "../services/category.service";
import { SequencePage } from "../shared/sequencepage";
import { ProductService } from "../services/product.services";
import { HttpRequest, HttpEventType, HttpResponse, HttpClient } from "@angular/common/http";
import { ReportService } from "../services/report.services";

@Component({
  selector: "user-point",
  templateUrl: "./userpointreport.component.html",
})
  // Wallet point
export class UserPointComponent implements OnInit, OnChanges {


  @Input()
  public data: SequencePage<WalletPoint>;

  public pageIndex: number = 1;
  public pageSize: number = 100;

  public pageLinks: number[] = [];
  public hasPage: boolean;

  public WalletPoints: WalletPoint[] = new Array<WalletPoint>();


  public formData: FormData = new FormData();
  public uploadProgress: number;
  public fileName: string;

  public walletPoint: WalletPoint = new WalletPoint();


  constructor(private services: ReportService, private router: Router,
    public toastr: ToastsManager, private vcr: ViewContainerRef, private loaderService: LoaderService, private http: HttpClient) {
    this.toastr.setRootViewContainerRef(vcr);
  }

  ngOnInit(): void {
    this.LoadFirstPage();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['products']) {
      // this.products = this.Get(0);
    }
  }

  LoadFirstPage() {
    var get = this.Get(0);
    this.hasPage = this.HasNoPage();
    return get;
  }

  LoadLastPage() {

    if (this.data) {
      return this.Get((Math.ceil(this.data.SequenceLength / this.pageSize) - 1));
    }
  }

  HasNoPage(): boolean {
    var result: boolean = this.data == null || typeof this.data == 'undefined' || this.data.Page.length == 0;
    return result;
  }

  public Get(pageIndex?: number): Array<WalletPoint> {

    var self = this;
    this.loaderService.display(true);

    this.services.GetWalletPoints(pageIndex, this.pageSize).subscribe(resp => {
      var _page = resp.Result;

      if (_page.PageSize == 0) {
        //<-- debug stuff
        self.pageLinks = [];
      }
      else {
        self.data = new SequencePage<WalletPoint>(_page.Page, _page.SequenceLength, _page.PageSize, _page.PageIndex);
        self.pageLinks = self.data.AdjacentIndexes(2);
        console.log(self.pageLinks);
        self.WalletPoints = self.data.Page;
      }
      this.loaderService.display(false);
      console.log(self.walletPoint);
      return self.walletPoint;
    }, err => {
      self.data = new SequencePage<WalletPoint>([], 0);
      self.pageLinks = self.data.AdjacentIndexes(2);

      //this.toastr.error('Operation failed e.g UnAuthorized or Network Issue');
      this.loaderService.display(false);
      return self.WalletPoints;
    });
    return self.WalletPoints;
  }

  public onDownload(id: number) {
    this.toastr.success("Operation successful");
  }

  public GetEnum(id: number): string {
    var enumValue = '';

    switch (id) {
      case 0:
        enumValue = 'Total Amount'
        break;
      case 1:
        enumValue = 'Question'
        break;
      case 2:
        enumValue = 'Product'
        break;
      case 3:
        enumValue = 'Watched Video'
        break;
      case 4:
        enumValue = 'Shared'
        break;
      default:
        enumValue = 'Others'
        break;
    }
    return enumValue;
  }
}



