import { Component, OnInit, ViewContainerRef, Input, OnChanges, SimpleChanges, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { Category, Product, UserQuestion } from "../shared/model";
import { ToastsManager } from "ng2-toastr";
import { LoaderService } from "../services/loader.service";
import { DataService } from "../services/data.service";
import { CategoryService } from "../services/category.service";
import { SequencePage } from "../shared/sequencepage";
import { ProductService } from "../services/product.services";
import { HttpRequest, HttpEventType, HttpResponse, HttpClient } from "@angular/common/http";
import { QuestionService } from "../services/question.services";
import { retry } from "rxjs/operators/retry";

@Component({
  selector: "question",
  templateUrl: "./question.component.html",
})

export class QuestionComponent implements OnInit, OnChanges {



  public product: Product = new Product();

  @Input()
  public data: SequencePage<UserQuestion>;

  public pageIndex: number = 1;
  public pageSize: number = 100;

  public pageLinks: number[] = [];
  public hasPage: boolean;

  public questions: UserQuestion[] = new Array<UserQuestion>();

  public formData: FormData = new FormData();
  public uploadProgress: number;
  public fileName: string;

  public question: UserQuestion = new UserQuestion();

  private Id: number;
  private Question: string;
  private OptionA: string;
  private OptionB: string;
  private OptionC: string;
  private OptionD: string;
  private Answer: string;

  constructor(private services: QuestionService, private router: Router,
    public toastr: ToastsManager, private vcr: ViewContainerRef, private loaderService: LoaderService, private http: HttpClient) {
    this.toastr.setRootViewContainerRef(vcr);
  }

  ngOnInit(): void {
    this.LoadFirstPage();
    
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['products']) {
      // this.products = this.Get(0);
    }
  }

  LoadFirstPage() {
    var get = this.Get(0);
    this.hasPage = this.HasNoPage();
    return get;
  }

  LoadLastPage() {

    if (this.data) {
      return this.Get((Math.ceil(this.data.SequenceLength / this.pageSize) - 1));
    }
  }

  HasNoPage(): boolean {
    var result: boolean = this.data == null || typeof this.data == 'undefined' || this.data.Page.length == 0;
    return result;
  }

  public Get(pageIndex?: number): Array<UserQuestion> {

    var self = this;
    this.loaderService.display(true);

    this.services.GetQuestionPage(pageIndex, this.pageSize).subscribe(resp => {
      var _page = resp.Result;
      console.log(_page);
      if (_page.PageSize == 0) {
        //<-- debug stuff
        self.pageLinks = [];
      }
      else {
        self.data = new SequencePage<UserQuestion>(_page.Page, _page.SequenceLength, _page.PageSize, _page.PageIndex);

        self.pageLinks = self.data.AdjacentIndexes(2);
        console.log(self.pageLinks);
        self.questions = self.data.Page;
        return self.questions;
      }
      this.loaderService.display(false);
    }, err => {
      self.data = new SequencePage<UserQuestion>([], 0);
      self.pageLinks = self.data.AdjacentIndexes(2);
      this.toastr.error('Operation failed e.g UnAuthorized or Network Issue');
      this.loaderService.display(false);
    });
    return self.questions;
  }

  public onUpdate(id: number) {
    var self = this;

    this.services.getQuestionById(id).subscribe(c => {

      self.question = c.Result;
      self.question.Question = c.Result.Question;
      self.question.OptionA = c.Result.OptionA;
      self.question.OptionB = c.Result.OptionA;
      self.question.OptionC = c.Result.OptionA;
      self.question.OptionD = c.Result.OptionA;
      self.question.Answer = c.Result.Answer;
      self.question.Id = c.Result.Id;

      console.log(c.Result);
    })
    //this.product = this.products.filter(c => c.ProductId == id).pop();//.filter(n => this.name = n.Name);
    console.log(self.product);
   // this.router.navigate(['/quesion']);
  }

  public onDelete(id: number) {
    var op = confirm('Are you sure?');
    var self = this;

    if (op) {

      this.services.DeleteQuestion(id).subscribe(c => {
        self.Get(0);
        this.toastr.success("Operation Successful");
        //this.router.navigate(['/question']);
      });
    }
  }


  public Ok() {

    var self = this;
    if (!self.question.Question) {
      this.toastr.error("Error, question cannot be empty ");
      return;
    }

    if (!self.question.OptionA) {
      this.toastr.error("Error, option A cannot be empty ");
      return;
    }

    if (!self.question.OptionB) {
      this.toastr.error("Error, option B cannot be empty ");
      return;
    }

    if (!self.question.OptionC) {
      this.toastr.error("Error, option C cannot be empty ");
      return;
    }

    if (!self.question.OptionD) {
      this.toastr.error("Error, option D cannot be empty ");
      return;
    }

    if (!self.question.Answer) {
      this.toastr.error("Error, Answer cannot be empty ");
      return;
    }

    this.loaderService.display(true);

    self.services.AddOrUpdateQuestion(self.question).subscribe(c => {
       self.Get(0);
      console.log(self.question);
      self.question.Answer = '';
      self.question.Id = 0;
      self.question.OptionA = '';
      self.question.OptionB = '';
      self.question.OptionC = '';
      self.question.OptionD = '';
      self.question.Question = ''

      if (c.Succeeded) {
        self.question = new UserQuestion();
        self.toastr.success('Operation is successful');
      }
      else {
        self.toastr.error('Operation failed');
      }
      self.loaderService.display(false);

     
    }, error => {
      self.Get(0);
      self.toastr.error('Operation failed,please check the inputs');
    });

  }
}



