import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from "rxjs";
import "rxjs/add/operator/map";
import { Category, Operation, Product, Genre, Video } from '../shared/model';
import { HttpClient, HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { StorageService } from './storage.service';
import { Constants } from '../shared/Constants';
import { Router } from '@angular/router';
import { catchError, retry } from 'rxjs/operators';
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import { DataService } from './data.service';
import { SequencePage } from '../shared/sequencepage';

@Injectable()
export class VideoService {

  private Genres: Genre[] = new Array<Genre>();
  private Videos: Video[] = new Array<Video>();

  constructor(private data: DataService, private storage: StorageService) {

  }

  // Genre .. 
  public GetGenre(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<Genre>>> {

    var url = `/api/video/genre/${pageIndex}/${pageSize}`;
    console.log(url);
    return this.data.Get<SequencePage<Genre>>(url);
  }

  public getGenre(): Observable<Operation<Genre[]>> {
    return this.data.Get<Genre[]>("/api/video/genre");
  }

  public getGenreById(id: number): Observable<Operation<Genre>> {
    return this.data.Get<Genre>("/api/video/genre/" + id);
  }

  public AddOrUpdateGenre(model: Genre): Observable<Operation<Genre>> {
    let url = `/api/video/genre`;
    var result = this.data.Post<Genre>(url, model);
    return result;
  }

  public DeleteGenre(id: number): Observable<Operation<Genre>> {

    let url = '/api/video/genre/' + id;

    return this.data.Delete<Genre>(url);
  }

  // video  ...

  public GetVideos(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<Video>>> {

    var url = `/api/video/${pageIndex}/${pageSize}`;
    console.log(url);
    return this.data.Get<SequencePage<Video>>(url);
  }

  public getVideo(): Observable<Operation<Video[]>> {
    return this.data.Get<Video[]>("/api/video");
  }

  public getVideoById(id: number): Observable<Operation<Video>> {
    return this.data.Get<Video>("/api/video/" + id);
  }

  public AddOrUpdateVideo(model: Video): Observable<Operation<Video>> {
    let url = `/api/video`;
    var result = this.data.Post<Video>(url, model);
    return result;
  }

  public DeleteVideo(id: number): Observable<Operation<Video>> {

    let url = '/api/video/' + id;

    return this.data.Delete<Video>(url);
  }
}



