import * as _ from "lodash"
import { expand } from "rxjs/operator/expand";
//import { publicDecrypt } from "crypto";


export class Token {
  public token: string;
  public expiration: Date;
  public user: User;

  public get loginRequired(): boolean {
    return (this.token.length == 0 || this.expiration > new Date());
  }
}
export class Operation<T> {
  Result: T;
  Succeeded: boolean;
  Message: string;
}
export class Alert {
  type: AlertType;
  message: string;
}

export enum AlertType {
  Success,
  Error,
  Info,
  Warning
}

export class Category {
  public CategoryId: number;
  public Name: string;
  public Icon: string;
  public CreatedDate: Date;
  public CreatedBy: number;
  public ModifiedDate: Date;
  public ModifiedBy: number;
  public Status: boolean;
}

export class Product {
  public ProductId: number;
  public ProductName: string;
  public ImageUrl: string;
  public Price: string;
  public CategoryId: number;
  public Category: Category;
  public CreatedDate: Date;
  public CreatedBy: number;
  public ModifiedDate: Date;
  public ModifiedBy: number;
  public Status: boolean;
}

export class Question {

  public Id: number;
  public Question: string;
  public Answer: number;
  public OptionA: string;
  public OptionB: string;
  public OptionC: string;
  public OptionD: string;
}

export class Advert {
  AdvertId: number;
  Name: string;
  Description: string;
  ImageUrl: string;
}

export class UserLogin {
  public Username: string;
  public Password: string;
  public RememberMe: boolean;

}

export class User {

  public Title: string;
  public UserId: number;
  public FirstName: string;
  public LastName: string;
  public MiddelName: string;
  public Mobile: string;
  public Gender: number;
  public Address: string;
  public EmailAddress: string;
  public Password: string;
  public Salt: string;
  public IsEmailConfirmed: boolean;
  public IsPhoneConfirmed: boolean;
  public IsLocked: boolean;
  public Username: string;
  public FullName: string;
  public LastLoginDate: Date;
  public Channel: RegChannel;
  public ApplicationCode: string;
  public DateOfBirth: Date;
  public UserStatus: UserStatus;
  public role: string;
  public ConfirmPassword: string;

}

export class PasswordChange {
  public UserId: number;
  public OldPassword: string;
  public NewPassword: string;
}
enum RegChannel {
  IsMobile = 1,
  IsWeb = 2
}

enum UserStatus { Pending, Verified, Blocked }

export class UserRole {
  public UserRoleId: number;
  public RoleId: number;
  public UserId: number;
  public User: User;
  public Role: Role;

}

export class Role {

  public RoleId: number;
  public Name: string;

}

export class UserQuestion {

  public Id: number;
  public Question: string;
  public Question1: string;
  public OptionA: string;
  public OptionB: string;
  public OptionC: string;
  public OptionD: string;
  public Answer: string;

}

export class Genre {
  public Id: number;
  public VideoGenre: string;
}

export class Video {
  public Id: number;
  public Title: string;
  public GenreId: number;
  public Genre: Genre;
  public Author: string;
  public Views: number;
  public VideoUrl: string;
  public Points: number;
  public ThumbNail: string;
}

export class WalletPoint {

  public Id: number;
  public FirstName: string;
  public LastName: string;
  public Point: number;
  public ItemPurchased: string;
  public TotalAmount: number;
  public Channel: PointChannel;
  public CreatedDate: Date;
}

export enum PointChannel {
  IsTotalAmount = 1,
  IsQuestion = 2,
  IsProduct = 3,
  IsVideoWatched = 4,
  Shared = 5
}

export class Dashboard {
  public CategoryCount: number;
  public ProductCount: number;
  public AdvertCount: number;
  public VideoCount: number;
  public UsersCount: number;
  public QuestionCount: number;
  public ReceiptCount: number;
  public TotalShareAndEarnCount: number;
  public TotalWatchEarnCount: number;
  public TotalSurveyAnsweredCount: number;
  public ReceiptModel: Array<Receipt> = new Array<Receipt>();
}

export class Receipt {

  public CategoryName: string;
  public ReceiptId: number;
  public MerchantName: string;
  public Category: Category;
  public Date: Date;
  public Currency: string;
  public TotalAmount: number;
  public TotalTax: number;
  public ReceiptRef: string;
  public PointValue: number;
  public UserId: number;
  public UserModel: User;
  public FilePath: string;
  public ContentLength: number;
  public ContentType: string;
  public ReceiptData: string;
  public ReceiptStatus: boolean;
}

export class WalletModel {
  public ReceiptId: number;
  public MerchantName: string;
 // public Category: string;
  public CategoryName: string;
  public Date: Date;
  public Currency: string;
  public TotalAmount: number;
  public TotalTax: number;
  public ReceiptRef: string;
  public PointValue: number;
  public UserId: number;
  public UserModel: User;
  public FilePath: string;
  public ContentLength: number;
  public ContentType: string
  public ReceiptData: string;
  public FirstName: string
  public LastName: string;
  public ReceiptStatus: boolean;
}
export class UserPointEarnedModel {
  public LastName: string;
  public FirstName: string;
  public MerchantName: string;
  public Mobile: string;
  public Point: number;
  public Channel: PointChannel;
  public CreatedDate: Date;
  public TotalAmount: number;
  public TotalTax: number;
  public ReceiptData: number;

}



