import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from "rxjs";
import "rxjs/add/operator/map";
import { Category, Operation, Product, UserQuestion, Question, WalletPoint, User, UserPointEarnedModel, Receipt, WalletModel } from '../shared/model';
import { HttpClient, HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { StorageService } from './storage.service';
import { Constants } from '../shared/Constants';
import { Router } from '@angular/router';
import { catchError, retry } from 'rxjs/operators';
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import { DataService } from './data.service';
import { SequencePage } from '../shared/sequencepage';

@Injectable()
export class ReportService {

  private question: UserQuestion[] = new Array<UserQuestion>();

  constructor(private data: DataService, private storage: StorageService) {

  }

  public GetWalletPoints(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<WalletPoint>>> {

    var url = `/api/report/${pageIndex}/wallet/${pageSize}`;
    return this.data.Get<SequencePage<WalletPoint>>(url);
  }

  public GetUsers(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<User>>> {

    var url = `/api/report/${pageIndex}/users/${pageSize}`;
    return this.data.Get<SequencePage<User>>(url);
  }

  public GetUserPointViaSharing(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<UserPointEarnedModel>>> {

    var url = `/api/report/${pageIndex}/sharing/${pageSize}`;
    return this.data.Get<SequencePage<UserPointEarnedModel>>(url);
  }

  public GetUserPointViewatchedVideo(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<UserPointEarnedModel>>> {

    var url = `/api/report/${pageIndex}/video/${pageSize}`;
    return this.data.Get<SequencePage<UserPointEarnedModel>>(url);
  }

  public GetUserPointViaProductSurvey(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<UserPointEarnedModel>>> {

    var url = `/api/report/${pageIndex}/survey/${pageSize}`;
    return this.data.Get<SequencePage<UserPointEarnedModel>>(url);
  }

  public GetUserReceipts(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<WalletModel>>> {

    var url = `/api/report/${pageIndex}/receipt/${pageSize}`;
    return this.data.Get<SequencePage<WalletModel>>(url);
  }

  public GetUserReceiptById(id: number): Observable<Operation<Receipt>> {

    var url = `/api/receipt/user-receipt/${id}`;
    return this.data.Get<Receipt>(url);
  }

  // not a reporting activity . 
  public DeleteUserReceiptById(id: number): Observable<Operation<Receipt>> {

    var url = `/api/receipt/${id}`;
    return this.data.Delete<Receipt>(url);
  }

  //user-point
  public UpdateUserReceipt(model: Receipt): Observable<Operation<Receipt>> {

    var url = `/api/receipt/user-point`;
    return this.data.Post<Receipt>(url, model);
  }
}



