import { Component, OnInit, ViewContainerRef, Input, OnChanges, SimpleChanges, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { Category, Product, Video, Genre } from "../shared/model";
import { ToastsManager } from "ng2-toastr";
import { LoaderService } from "../services/loader.service";
import { DataService } from "../services/data.service";
import { SequencePage } from "../shared/sequencepage";
import { HttpRequest, HttpEventType, HttpResponse, HttpClient } from "@angular/common/http";
import { VideoService } from "../services/video.services";

@Component({
  selector: "videos",
  templateUrl: "./videos.component.html",
})

export class VideoComponent implements OnInit, OnChanges {



  public video: Video = new Video();

  @Input()
  public data: SequencePage<Video>;

  public pageIndex: number = 1;
  public pageSize: number = 100;

  public pageLinks: number[] = [];
  public hasPage: boolean;

  public videos: Video[] = new Array<Video>();

  public formData: FormData = new FormData();
  public uploadProgress: number;
  public uploadProgress2: number;
  public fileName: string;

  public genres: Genre[] = new Array<Genre>();

  constructor(private services: VideoService, private router: Router,
    public toastr: ToastsManager, private vcr: ViewContainerRef, private loaderService: LoaderService, private http: HttpClient) {
    this.toastr.setRootViewContainerRef(vcr);
  }

  ngOnInit(): void {
    var self = this;

    this.LoadFirstPage();
    this.services.getGenre().subscribe(c => {
      self.genres = c.Result;

    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.videos = this.Get(0);
    if (changes['video']) {

    }
  }

  LoadFirstPage() {
    var get = this.Get(0);
    this.hasPage = this.HasNoPage();
    return get;
  }

  LoadLastPage() {

    if (this.data) {
      return this.Get((Math.ceil(this.data.SequenceLength / this.pageSize) - 1));
    }
  }

  HasNoPage(): boolean {
    var result: boolean = this.data == null || typeof this.data == 'undefined' || this.data.Page.length == 0;
    return result;
  }

  public Get(pageIndex?: number): Array<Video> {

    var self = this;
    this.loaderService.display(true);

    this.services.GetVideos(pageIndex, this.pageSize).subscribe(resp => {
      var _page = resp.Result;

      if (_page.PageSize == 0) {
        //<-- debug stuff
        self.pageLinks = [];
      }
      else {

        self.data = new SequencePage<Video>(_page.Page, _page.SequenceLength, _page.PageSize, _page.PageIndex);
        self.pageLinks = self.data.AdjacentIndexes(2);
        console.log(self.pageLinks);
        self.videos = self.data.Page;
      }

      this.loaderService.display(false);
      return self.videos;
    }, err => {
      self.data = new SequencePage<Video>([], 0);
      self.pageLinks = self.data.AdjacentIndexes(2);
      //this.toastr.error('Operation failed e.g UnAuthorized or Network Issue');
      this.loaderService.display(false);
      return self.videos;
    });
    return self.videos;
  }

  // @ViewChild("fileInput") fileInput;

  //addFile(): void {
  //  let fi = this.fileInput.nativeElement;
  //  if (fi.files && fi.files[0]) {
  //    let fileToUpload = fi.files[0];
  //    //this.uploadService
  //    //  .upload(fileToUpload)
  //    //  .subscribe(res => {
  //    //    console.log(res);
  //    //  });
  //  }
  //}


  public onUpdate(id: number) {

    var self = this;

    this.services.getVideoById(id).subscribe(c => {
      self.video = c.Result;
      console.log(c.Result);
    })
    //this.product = this.products.filter(c => c.ProductId == id).pop();//.filter(n => this.name = n.Name);
    console.log(self.video);
    this.router.navigate(['/video-upload']);
  }

  public onDelete(id: number) {

    var op = confirm('Are you sure?');
   
    if (op) {
      this.services.DeleteVideo(id).subscribe(c => {
        this.videos = this.Get(0);
        this.toastr.success("Operation Successful");
        this.router.navigate(['/video-upload']);
      });
    }
   

  }

  onFileChanged(files) {
    if (!files) {
      this.toastr.error("Error, please check the file inputs ");
      return;

    }

    // const formData = new FormData();

    for (let file of files) {
      this.formData.append(file.name, file);
      this.fileName = file.name;
    }

    //this.files = event.target.files;
  }



  public Ok() {

    var self = this;

    if (!this.video.Author) {
      this.toastr.error("Error, author cannot be empty ");
      return;
    }
    if (!this.video.Title) {
      this.toastr.error("Error, title cannot be empty ");
      return;
    }

    //if (!this.video.Points) {
    //  this.toastr.error("Error, point cannot be empty ");
    //  return;
    //}
    if (!this.video.GenreId) {
      this.toastr.error("Error, please select genre Id ");
      return;
    }

    this.loaderService.display(true);

    var self = this;



    self.services.AddOrUpdateVideo(self.video).subscribe(c => {

      if (c.Succeeded) {
        console.log(self.formData)
        const req = new HttpRequest('POST', `api/video/file/${c.Result.Id}`, self.formData, {
          reportProgress: true,
        });

        self.toastr.success('Operation is successful');

        self.http.request(req).subscribe(event => {
          self.videos = self.Get(0);

          if (event.type === HttpEventType.UploadProgress) {
            self.uploadProgress = Math.round(100 * event.loaded / event.total);

            // self.product.ProductId = 0;
            self.loaderService.display(false);
          }
          else if (event instanceof HttpResponse) {
            console.log('Files uploaded!');
            // self.toastr.success('Operation is successful');
          }
          self.loaderService.display(false);
        });
      }
      else {
        self.toastr.error('Operation failed');
      }
      self.loaderService.display(false);

      self.formData = new FormData();

    }, error => {
      self.toastr.error('Operation failed,please check the inputs');
    });





  }

}



