import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from "rxjs";
import "rxjs/add/operator/map";
import { UserLogin, User, Token, Operation, PasswordChange } from '../shared/model';
import { HttpClient, HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { StorageService } from './storage.service';
import { Constants } from '../shared/Constants';
import { Router } from '@angular/router';
import { catchError, retry } from 'rxjs/operators';
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import { DataService } from './data.service';
import { SequencePage } from '../shared/sequencepage';

@Injectable()
export class AccountService {
 
  private users: User[] = [];
  public token: Token = new Token();
  public userId: number;

  constructor(private data: DataService, private storage: StorageService, private router: Router) {

  }


  // Get token

  public getUserToken(user: UserLogin): Observable<Operation<Token>> {
    let url = '/api/account/token';
    var result = this.data.PostWithOutToken<Token>(url, user);
    console.log(result);
    return result;
  }

  // user login
  public userLogin(model: UserLogin): Observable<Operation<User>> {
    let url = `/api/account/user`;
    var r = this.data.Post<User>(url, model);
    return r;
  }


  // User account details

  // get user list..

  public getUserPage(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<User>>> {

    var url = `/api/account/${pageIndex}/user/${pageSize}`;
    var result = this.data.Get<SequencePage<User>>(url);

    return result;
  }
  //public getUserPage(): Observable<Operation<User[]>> {
  //  return this.data.Get<User[]>("/api/account/users");
  //}
  public getUsers(): Observable<Operation<User[]>> {
    return this.data.Get<User[]>("/api/account/users");
  }

  // get user by user id
  public getUser(userId: number): Observable<Operation<User>> {

    let url = `/api/account/user/${userId}`;
    var r = this.data.Get<User>(url);
    return r;
  }

  // delete funtnx
  public deleteUser(id: number): Observable<Operation<User>> {

    let url = '/api/account/' + id;

    return this.data.Delete<User>(url);
  }

  // register user 
  public registerUser(model: User): Observable<Operation<User>> {
    let url = '/api/account/register';
    return this.data.Post<User>(url, model);
  }

  // update profile
  public updateProfile(model: User): Observable<Operation<User>> {

    let url = `/api/account/update-profile`;

    return this.data.Put<User>(url, model);
  }

  // change password ..
  public changepassword(model: PasswordChange) {

    let url = `/api/account/change-password`;
    return this.data.Put<User>(url, model);
  }
}



