import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from "rxjs";
import "rxjs/add/operator/map";
import { Category, Operation } from '../shared/model';
import { HttpClient, HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { StorageService } from './storage.service';
import { Constants } from '../shared/Constants';
import { Router } from '@angular/router';
import { catchError, retry } from 'rxjs/operators';
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import { DataService } from './data.service';
import { SequencePage } from '../shared/sequencepage';

@Injectable()
export class CategoryService {

  private categories: Category[] = new Array<Category>();

  constructor(private data: DataService, private storage: StorageService) {

  }

  public GetCategories(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<Category>>> {

    var url = `/api/category/${pageIndex}/${pageSize}`;
    var result = this.data.Get<SequencePage<Category>>(url);
    
    return result;
  }

  public getCategory(): Observable<Operation<Category[]>> {
    return this.data.Get<Category[]>("/api/category");
  }

  public AddOrUpdateCategory(category: Category): Observable<Operation<Category>> {
    let url = `/api/category`;
    return this.data.Post<Category>(url, category);
  }

  public DeleteCategory(categoryId: number): Observable<Operation<Category>> {

    let url = '/api/category/' + categoryId;

    return this.data.Delete<Category>(url);
  }


}



