import { Component, OnInit, ViewContainerRef, Input, OnChanges, SimpleChanges, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { Category, Product, Video, Genre, User } from "../shared/model";
import { ToastsManager } from "ng2-toastr";
import { LoaderService } from "../services/loader.service";
import { DataService } from "../services/data.service";
import { SequencePage } from "../shared/sequencepage";
import { HttpRequest, HttpEventType, HttpResponse, HttpClient } from "@angular/common/http";
import { VideoService } from "../services/video.services";
import { AccountService } from "../services/account.service";
import { StorageService } from "../services/storage.service";

@Component({
  selector: "profile",
  templateUrl: "./profile.component.html",
})

export class ProfileComponent implements OnInit, OnChanges {

  public user: User = new User();

  @Input()
  public data: SequencePage<User>;

  public pageIndex: number = 1;
  public pageSize: number = 100;

  public pageLinks: number[] = [];
  public hasPage: boolean;

  public users: User[] = new Array<User>();

  public formData: FormData = new FormData();
  public uploadProgress: number;
  public fileName: string;


  constructor(private services: AccountService, private router: Router,
    public toastr: ToastsManager, private vcr: ViewContainerRef, private loaderService: LoaderService, private storage: StorageService ,private http: HttpClient) {
    this.toastr.setRootViewContainerRef(vcr);
  }


  ngOnInit(): void {
    var self = this;

    this.services.getUser(this.storage.User.UserId).subscribe(c => {
      self.user = c.Result;

    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['user']) {

    }
  }

  public onUpdate(id: number) {

    var self = this;

    this.services.getUser(id).subscribe(c => {
      self.user = c.Result;
      console.log(c.Result);
    })
    //this.product = this.products.filter(c => c.ProductId == id).pop();//.filter(n => this.name = n.Name);
    console.log(self.user);
    this.router.navigate(['/profile']);
  }

  public Ok() {

    var self = this;

    if (!this.user.FirstName) {
      this.toastr.error("Error, FirstName cannot be empty ");
      return;
    }
    if (!this.user.LastName) {
      this.toastr.error("Error, LastName cannot be empty ");
      return;
    }

    if (!this.user.Mobile) {
      this.toastr.error("Error, Mobile cannot be empty ");
      return;
    }
    if (!this.user.EmailAddress) {
      this.toastr.error("Error, please select EmailAddress ");
      return;
    }
    if (this.user.Password != this.user.ConfirmPassword) {
      this.toastr.error("Error, please password must be equals confirm password");
      return;
    }

    this.loaderService.display(true);

    var self = this;

    self.services.registerUser(this.user).subscribe(c => {

      if (c.Succeeded) {
        this.user.Password = null;
        this.user.ConfirmPassword = null;

        self.toastr.success('Operation successful');
      }
      else {
        self.toastr.error('Operation failed');
      }
      self.loaderService.display(false);

    }, error => {
      self.toastr.error('Operation failed,please check the inputs');
    });





  }

}



