import { Component, OnInit } from '@angular/core';
import { LoaderService } from '../services/loader.service';
import { StorageService } from '../services/storage.service';
import { User } from './model';
import { Constants } from './Constants';
import { LocalStorageService } from 'angular-2-local-storage';
import { Router } from '@angular/router';

@Component({
  selector: 'customheader',
  templateUrl: './customheader.component.html'
})

export class CustomHeaderComponent implements OnInit {

  public firstName: string;
  public lastName: string;

  constructor(
    private loaderService: LoaderService, private storage: StorageService, private localStorageService: LocalStorageService, private router: Router) {
  }

  ngOnInit() {
    try {
      if (this.storage.User != null) {
        this.firstName = this.storage.User.FirstName;
        this.lastName = this.storage.User.LastName;
      }

    }
    catch
    {
      this.storage.Clear();
      this.router.navigate(['/login']);
    }

    //http call starts
    this.loaderService.display(true);
    //http call ends
    this.loaderService.display(false);
  }
}



