import { Component, Injectable } from "@angular/core";
import { Constants } from "../shared/Constants";
import { User, Token } from "../shared/model";
import { LocalStorageService } from "angular-2-local-storage";


@Injectable()
export class StorageService {


  public user: User;
  public token: Token;

  //Gets currently logged in User

  constructor(private localStorageService: LocalStorageService) {

  }

  get User() {
    if (!this.user) {
      this.user = this.Get<User>(Constants.UserKey);
     // this.user = this.localStorageService.get<User>(Constants.UserKey);
    }
    return this.user;
  }

  set User(user: User) {
    this.user = user;
    this.Store(Constants.UserKey, user);
  }

  get Token() {
    if (!this.token) {
       this.token = this.Get<Token>(Constants.OAuthTokenKey);
      //this.token = this.localStorageService.get<Token>(Constants.OAuthTokenKey);
    }
    return this.token;
  }

  set Token(token: Token) {
    this.token = token;
    this.Store(Constants.OAuthTokenKey, token);
  }


  //Local Storage Methods
  private Store(key, value) {
    return window.localStorage.setItem(key, JSON.stringify(value));
    //return this.localStorageService.set(key, JSON.stringify(value));
  }

  private Remove(key: string) {
    window.localStorage.removeItem(key);
    //this.localStorageService.remove(key);
  }

  private Get<T>(key: string) {
    return <T>JSON.parse(window.localStorage.getItem(key));
    //return <T>JSON.parse(this.localStorageService.get(key));

  }

  Clear() {
    window.localStorage.clear();
   // this.localStorageService.clearAll();
  }
}



