import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from "rxjs";
import "rxjs/add/operator/map";
import { Category, Operation, Product, Advert } from '../shared/model';
import { HttpClient, HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { StorageService } from './storage.service';
import { Constants } from '../shared/Constants';
import { Router } from '@angular/router';
import { catchError, retry } from 'rxjs/operators';
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import { DataService } from './data.service';
import { SequencePage } from '../shared/sequencepage';


@Injectable()
export class AdvertService {

  constructor(private data: DataService, private storage: StorageService) {

  }

  public GetAdverts(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<Advert>>> {

    var url = `/api/advert/${pageIndex}/${pageSize}`;
    console.log(url);
    return this.data.Get<SequencePage<Advert>>(url);
  }


  public getAdvert(): Observable<Operation<Advert[]>> {
    return this.data.Get<Advert[]>("/api/advert");
  }

  public AddOrUpdateAdvert(advert: Advert): Observable<Operation<Advert>> {
    let url = `/api/advert`;
    var result = this.data.Post<Advert>(url, advert);
    return result;
  }

  public DeleteAdvert(advertId: number): Observable<Operation<Advert>> {

    let url = '/api/advert/' + advertId;

    return this.data.Delete<Advert>(url);
  }


}



