import { BrowserModule } from '@angular/platform-browser';
import { NgModule, Component } from '@angular/core';
import { HttpClientModule } from "@angular/common/http"

// router .. 
import { RouterModule } from "@angular/router";
import { FormsModule } from "@angular/forms";
import { AppComponent } from './app.component';

import { Login } from "./accounts/login.component";
import { DashboardComponent } from './dashboard/dashboard.component';
import { CategoryComponent } from './category/category.component';
import { ToastModule } from 'ng2-toastr/ng2-toastr';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { CustomHeaderComponent } from './shared/customheader.component'
import { FooterComponent } from './shared/footer.component';
import { Logout } from './accounts/logout.component';
import { LocalStorageModule } from 'angular-2-local-storage';
import { StorageServiceModule } from 'angular-webstorage-service';
import { FilterPipe } from './shared/custompipe';
import { ProductComponent } from './Products/product.component';
import { AdvertComponent } from './advert/advert.component';
import { QuestionComponent } from './questions/question.component';
import { GenreComponent } from './videos/genre.component';
import { VideoComponent } from './videos/video.component';
import { UserComponent } from './accounts/user.component'
import { ProfileComponent } from './accounts/profile.component';
import { UserPointComponent } from './reports/userpointreport.component';
import { GetUserComponent } from './reports/getusers.component';
import { ProductSurveyComponent } from './reports/productsurvey.component';
import { UserPointSharingComponent } from './reports/userpointsharing.component';
import { UserReceiptsComponent } from './reports/user-receipts.component';
import { WatchedVideosComponent } from './reports/watched-video.component';
import { ManualReceiptComponent } from './reports/manualreceipt.component'

import { CategoryService } from './services/category.service';
import { DataService } from './services/data.service';
import { AccountService } from './services/account.service';
import { StorageService } from './services/storage.service';
import { LoaderService } from './services/loader.service';
import { ProductService } from './services/product.services';
import { DashboardService } from './services/dashboard.service';
import { AdvertService } from './services/advert.service';
import { QuestionService } from './services/question.services';
import { VideoService } from './services/video.services';
import { ReportService } from './services/report.services';


let routes = [
  { path: '', redirectTo: '/dashboard', pathMatch: 'full' },
  { path: 'dashboard', component: DashboardComponent },
  { path: 'login', component: Login },
  { path: 'category', component: CategoryComponent },
  { path: 'product', component: ProductComponent },
  { path: 'advert', component: AdvertComponent },
  { path: 'question', component: QuestionComponent },
  { path: 'genre', component: GenreComponent },
  { path: 'video-upload', component: VideoComponent },
  { path: 'create-user', component: UserComponent },
  { path: 'profile', component: ProfileComponent },
  { path: 'wallet-report', component: UserPointComponent },
  { path: 'registered-users-reports', component: GetUserComponent },
  { path: 'product-survey-report', component: ProductSurveyComponent },
  { path: 'shared-user-report', component: UserPointSharingComponent },
  { path: 'receipt-report', component: UserReceiptsComponent },
  { path: 'video-watched-report', component: WatchedVideosComponent },
  { path: 'manualreceipt', component: ManualReceiptComponent },
  { path: 'logout', component: Logout },
  { path: '**', component: DashboardComponent }
];

@NgModule({
  declarations: [
    AppComponent,
    DashboardComponent,
    Login,
    CustomHeaderComponent,
    FooterComponent,
    Logout,
    CategoryComponent,
    FilterPipe,
    ProductComponent,
    AdvertComponent,
    QuestionComponent,
    GenreComponent,
    VideoComponent,
    UserComponent,
    ProfileComponent,
    UserPointComponent,
    GetUserComponent,
    ProductSurveyComponent,
    UserPointSharingComponent,
    UserReceiptsComponent,
    WatchedVideosComponent,
    ManualReceiptComponent
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    FormsModule,
    RouterModule.forRoot(routes, {
      useHash: true,
      enableTracing: false // for debburgin route ..
    }),
    ToastModule.forRoot(),
    BrowserAnimationsModule,
    LocalStorageModule.withConfig({ prefix: 'my-app', storageType: 'localStorage' }),
    StorageServiceModule

  ],
  exports: [
    RouterModule,
    FilterPipe
  ],
  providers: [DataService, DashboardService, AccountService, StorageService,
    LoaderService, CategoryService, ProductService, AdvertService, QuestionService,
    VideoService, ReportService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



