import { Component, OnInit, ViewContainerRef, Input, OnChanges, SimpleChanges, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { Category, Product, Video, Genre, User } from "../shared/model";
import { ToastsManager } from "ng2-toastr";
import { LoaderService } from "../services/loader.service";
import { DataService } from "../services/data.service";
import { SequencePage } from "../shared/sequencepage";
import { HttpRequest, HttpEventType, HttpResponse, HttpClient } from "@angular/common/http";
import { VideoService } from "../services/video.services";
import { AccountService } from "../services/account.service";

@Component({
  selector: "users",
  templateUrl: "./user.component.html",
})

export class UserComponent implements OnInit, OnChanges {

  public user: User = new User();

  @Input()
  public data: SequencePage<User>;

  public pageIndex: number = 1;
  public pageSize: number = 100;

  public pageLinks: number[] = [];
  public hasPage: boolean;

  public users: User[] = new Array<User>();

  public formData: FormData = new FormData();
  public uploadProgress: number;
  public fileName: string;

  constructor(private services: AccountService, private router: Router,
    public toastr: ToastsManager, private vcr: ViewContainerRef, private loaderService: LoaderService, private http: HttpClient) {
    this.toastr.setRootViewContainerRef(vcr);
  }


  ngOnInit(): void {
    var self = this;

    this.LoadFirstPage();
    this.services.getUsers().subscribe(c => {
      self.users = c.Result;

    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    this.users = this.Get(0);
    if (changes['user']) {

    }
  }

  LoadFirstPage() {
    var get = this.Get(0);
    this.hasPage = this.HasNoPage();
    return get;
  }

  LoadLastPage() {

    if (this.data) {
      return this.Get((Math.ceil(this.data.SequenceLength / this.pageSize) - 1));
    }
  }

  HasNoPage(): boolean {
    var result: boolean = this.data == null || typeof this.data == 'undefined' || this.data.Page.length == 0;
    return result;
  }

  public Get(pageIndex?: number): Array<User> {

    var self = this;
    this.loaderService.display(true);

    this.services.getUserPage(pageIndex, this.pageSize).subscribe(resp => {
      var _page = resp.Result;

      if (_page.PageSize == 0) {
        //<-- debug stuff
        self.pageLinks = [];
      }
      else {

        self.data = new SequencePage<User>(_page.Page, _page.SequenceLength, _page.PageSize, _page.PageIndex);
        self.pageLinks = self.data.AdjacentIndexes(2);
        console.log(self.pageLinks);
        self.users = self.data.Page;
      }

      this.loaderService.display(false);
      return self.users;
    }, err => {
      self.data = new SequencePage<User>([], 0);
      self.pageLinks = self.data.AdjacentIndexes(2);
      //this.toastr.error('Operation failed e.g UnAuthorized or Network Issue');
      this.loaderService.display(false);
      return self.users;
      });
    return self.users;
  }

  public onUpdate(id: number) {

    var self = this;

    this.services.getUser(id).subscribe(c => {
      self.user = c.Result;
      console.log(c.Result);
    })
    //this.product = this.products.filter(c => c.ProductId == id).pop();//.filter(n => this.name = n.Name);
    console.log(self.user);
    this.router.navigate(['/create-user']);
  }

  public onDelete(id: number) {

    var op = confirm('Are you sure?');

    if (op) {
      this.services.deleteUser(id).subscribe(c => {
        this.users = this.Get(0);
        this.toastr.success("Operation Successful");
        this.router.navigate(['/create-user']);
      });
    }


  }

  onFileChanged(files) {
    if (!files) {
      this.toastr.error("Error, please check the file inputs ");
      return;

    }

    // const formData = new FormData();

    for (let file of files) {
      this.formData.append(file.name, file);
      this.fileName = file.name;
    }

    //this.files = event.target.files;
  }

  onFileUpload() {
    const formData = new FormData();
    //for (const file of this.files) {
    //  formData.append(name, file, file.name);
    //}
    //this.http.post('url', formData).subscribe(x => ....);
  }

  public Ok() {

    var self = this;

    if (!this.user.FirstName) {
      this.toastr.error("Error, FirstName cannot be empty ");
      return;
    }
    if (!this.user.LastName) {
      this.toastr.error("Error, LastName cannot be empty ");
      return;
    }

    if (!this.user.Mobile) {
      this.toastr.error("Error, Mobile cannot be empty ");
      return;
    }
    if (!this.user.EmailAddress) {
      this.toastr.error("Error, please select EmailAddress ");
      return;
    }
    if (this.user.Password != this.user.ConfirmPassword) {
      this.toastr.error("Error, please password must be equals confirm password");
      return;
    }

    this.loaderService.display(true);

    var self = this;

    self.services.registerUser(this.user).subscribe(c => {

      if (c.Succeeded) {
        self.users = self.Get(0);
        self.user = null;
        self.toastr.success('Operation successful');
      }
      else {
        self.toastr.error('Operation failed');
      }
      self.loaderService.display(false);

      self.formData = new FormData();

    }, error => {
      self.toastr.error('Operation failed,please check the inputs');
    });





  }

}



