import { Injectable } from '@angular/core';
import { Http, Response } from '@angular/http';
import { Observable } from "rxjs";
import "rxjs/add/operator/map";
import { Category, Operation, Product } from '../shared/model';
import { HttpClient, HttpResponse, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { StorageService } from './storage.service';
import { Constants } from '../shared/Constants';
import { Router } from '@angular/router';
import { catchError, retry } from 'rxjs/operators';
import { ErrorObservable } from 'rxjs/observable/ErrorObservable';
import { DataService } from './data.service';
import { SequencePage } from '../shared/sequencepage';

@Injectable()
export class ProductService {

  private categories: Category[] = new Array<Category>();
  private products: Product[] = new Array<Product>();

  constructor(private data: DataService, private storage: StorageService) {

  }

  public GetProducts(pageIndex: number, pageSize: number): Observable<Operation<SequencePage<Product>>> {

    var url = `/api/product/${pageIndex}/page/${pageSize}`;
    console.log(url);
    return this.data.Get<SequencePage<Product>>(url);
  }


  public getProduct(): Observable<Operation<Product[]>> {
    return this.data.Get<Product[]>("/api/product");
  }

  public getProductById(id: number): Observable<Operation<Product>> {
    return this.data.Get<Product>("/api/product/" + id);
  }

  public AddOrUpdateProduct(product: Product): Observable<Operation<Product>> {
    let url = `/api/product`;
    var result = this.data.Post<Product>(url, product);
    return result;
  }

  public DeleteProduct(productId: number): Observable<Operation<Product>> {

    let url = '/api/product/' + productId;

    return this.data.Delete<Product>(url);
  }


}



