
import { Component, OnInit, ViewContainerRef, Input, OnChanges, SimpleChanges, ViewChild } from "@angular/core";
import { Router } from "@angular/router";
import { Category, Product, UserQuestion, WalletPoint, User, Receipt, WalletModel } from "../shared/model";
import { ToastsManager } from "ng2-toastr";
import { LoaderService } from "../services/loader.service";
import { DataService } from "../services/data.service";
import { CategoryService } from "../services/category.service";
import { SequencePage } from "../shared/sequencepage";
import { ProductService } from "../services/product.services";
import { HttpRequest, HttpEventType, HttpResponse, HttpClient } from "@angular/common/http";
import { ReportService } from "../services/report.services";

@Component({
  selector: "manual-reciept",
  templateUrl: "./manualreceipt.component.html",
})

export class ManualReceiptComponent implements OnInit, OnChanges {

  @Input()
  public data: SequencePage<WalletModel>;

  public pageIndex: number = 1;
  public pageSize: number = 100;

  public pageLinks: number[] = [];
  public hasPage: boolean;

  public formData: FormData = new FormData();
  public uploadProgress: number;
  public fileName: string;

  public reports: WalletModel[] = new Array<WalletModel>();
  public report: Receipt = new Receipt();
  public categories: Category[] = new Array<Category>();

  constructor(private services: ReportService, private router: Router,
    public toastr: ToastsManager, private categoryService: CategoryService, private vcr: ViewContainerRef, private loaderService: LoaderService, private http: HttpClient) {
    this.toastr.setRootViewContainerRef(vcr);
  }

  ngOnInit(): void {

    this.categoryService.getCategory().subscribe(c => {
      if (c.Succeeded) {
        this.categories = c.Result;
      }
    })
    this.LoadFirstPage();

  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes['manualreceipt']) {
      // this.products = this.Get(0);
    }
  }

  // Get category


  LoadFirstPage() {
    var get = this.Get(0);
    this.hasPage = this.HasNoPage();
    return get;
  }

  LoadLastPage() {

    if (this.data) {
      return this.Get((Math.ceil(this.data.SequenceLength / this.pageSize) - 1));
    }
  }

  HasNoPage(): boolean {
    var result: boolean = this.data == null || typeof this.data == 'undefined' || this.data.Page.length == 0;
    return result;
  }

  public Get(pageIndex?: number): Array<WalletModel> {

    var self = this;
    this.loaderService.display(true);

    this.services.GetUserReceipts(pageIndex, this.pageSize).subscribe(resp => {
      var _page = resp.Result;

      if (_page.PageSize == 0) {
        //<-- debug stuff
        self.pageLinks = [];
      }
      else {

        self.data = new SequencePage<WalletModel>(_page.Page, _page.SequenceLength, _page.PageSize, _page.PageIndex);
        self.pageLinks = self.data.AdjacentIndexes(2);
        console.log(self.pageLinks);
        self.reports = self.data.Page;
      }

      this.loaderService.display(false);
      return self.reports;
    }, err => {
      self.data = new SequencePage<WalletModel>([], 0);
      self.pageLinks = self.data.AdjacentIndexes(2);
      //this.toastr.error('Operation failed e.g UnAuthorized or Network Issue');
      this.loaderService.display(false);
      return self.reports;
    });
    return self.reports;
  }

  public onDownload(id: number) {
    this.toastr.success("Operation successful");
  }

  public GetEnum(id: number): string {
    var enumValue = '';

    switch (id) {
      case 0:
        enumValue = 'Total Amount'
        break;
      case 1:
        enumValue = 'Question'
        break;
      case 2:
        enumValue = 'Product'
        break;
      case 3:
        enumValue = 'Watched Video'
        break;
      case 4:
        enumValue = 'Shared'
        break;
      default:
        enumValue = 'Others'
        break;
    }
    return enumValue;
  }

  public onUpdate(id: number) {
    var self = this;
    this.services.GetUserReceiptById(id).subscribe(c => {
     
      self.report.CategoryName = c.Result.CategoryName;
      self.report.MerchantName = c.Result.MerchantName;
      self.report.ReceiptData = c.Result.ReceiptData;
      self.report.TotalAmount = c.Result.TotalAmount;
      self.report.TotalTax = c.Result.TotalTax;
      self.report.FilePath = c.Result.FilePath;
      self.report.ReceiptId = c.Result.ReceiptId;
    });
  }

  public onDelete(id: number) {

    this.services.DeleteUserReceiptById(id).subscribe(c => {

      if (c.Succeeded) {
        this.toastr.success("Operation  Successful");
      }
      else {
        this.toastr.error("Operation Successful");
      }
    })
  }

  public UpdateReceipt(model: Receipt) {

    var self = this;
    this.services.UpdateUserReceipt(model).subscribe(c => {

      if (c.Succeeded) {
        self.Get(0);
        this.toastr.success("Operation successful");
      }
      else {
        this.toastr.error("Operation failed " + c.Message);
      }

    });

  }
}



